<template>
	<gracePage :customHeader="false" :isLoading="pageLoading">
		<view slot="gBody" class="grace-body">
			<!-- <view class="grace-h4 grace-margin-top" style="padding:20rpx 0;">用户中心</view> -->
			<view class="grace-h4 grace-margin-top" style="padding:10rpx 0;"></view>
			<view class="grace-list" style="margin-top:10rpx; margin-bottom:30rpx;">
				<view class="items">
					<view class="icons ucenter-face"><image src="../../static/img/usercenter.png" mode="widthFix"></image></view>
					<view class="body">
						<view class="title">{{ userInfo.UserName }}</view>
						<view class="desc">{{ userInfo.NickName }}</view>
						<view class="desc">{{ userInfo.RealName }}</view>
					</view>
					<!-- <view class="arrow-right"></view> -->
				</view>
			</view>

			<view class="ucenter-line"></view>

<!-- 			<block v-if="currentProjectInfo.OrderStatusCode == 20">
				<view>
					<view>
						当前项目：
						<text>{{ currentProjectInfo.Name }}</text>
					</view>
				</view>

				<view class="grace-box-banner">
					<view class="garce-items">
						<view class="line1">{{ myFirstCounted + mySecondCounted + myThirdCounted }}</view>
						<view class="line2">已完成</view>
					</view>
					<view class="garce-items">
						<view class="line1">{{ myFirstTotal + mySecondTotal + myThirdTotal }}</view>
						<view class="line2">全部</view>
					</view>
				</view>

				<view>
					我的初盘进度：
					<text class="grace-green">{{ myFirstCounted }}</text>
					/{{ myFirstTotal }}
					<progress :percent="myFirstProgress" show-info />
				</view>
				<view v-if="currentProjectInfo.IsSecondEnabled == 1">
					<view>
						我的复盘进度：
						<text class="grace-green">{{ mySecondCounted }}</text>
						/{{ mySecondTotal }}
						<progress :percent="mySecondProgress" show-info />
					</view>
				</view>
				<view v-if="currentProjectInfo.IsThirdEnabled == 1">
					<view>
						我的三盘进度：
						<text class="grace-green">{{ myThirdCounted }}</text>
						/{{ myThirdTotal }}
						<progress :percent="myThirdProgress" show-info />
					</view>
				</view>

				<view class="ucenter-line"></view>
			</block> -->

			<view class="grace-list">
				<navigator open-type="reLaunch" class="items" url="changepassword">
					<view class="icons grace-icons icon-set grace-blue"></view>
					<view class="body"><view class="title">更改密码</view></view>
					<view class="arrow-right"></view>
				</navigator>

				<navigator open-type="reLaunch" class="items" url="changeuserinfo">
					<view class="icons grace-icons icon-user grace-blue"></view>
					<view class="body"><view class="title">用户信息</view></view>
					<view class="arrow-right"></view>
				</navigator>
			</view>
			<view class="ucenter-line"></view>

			<view class="grace-list">
				<navigator open-type="reLaunch" class="items" url="agreement">
					<view class="icons grace-icons icon-menu grace-blue"></view>
					<view class="body"><view class="title">用户服务使用协议</view></view>
					<view class="arrow-right"></view>
				</navigator>
				<navigator open-type="reLaunch" class="items" url="privacy">
					<view class="icons grace-icons icon-eye grace-blue"></view>
					<view class="body"><view class="title">用户隐私政策</view></view>
					<view class="arrow-right"></view>
				</navigator>
			</view>

			<view class="ucenter-line"></view>

			<view>
				<button v-if="!userIsLogin" type="primary" class="primary" @tap="bindLogin">登录</button>
				<button v-if="userIsLogin" type="default" @tap="bindLogout">退出登录</button>
			</view>
			<view class="ucenter-line"></view>
		</view>
	</gracePage>
</template>
<script>
export default {
	data() {
		return {
			userIsLogin: false,
			userInfo: {},
			currentProjectInfo: {},
			myFirstTotal: 0,
			mySecondTotal: 0,
			myThirdTotal: 0,
			myFirstCounted: 0,
			mySecondCounted: 0,
			myThirdCounted: 0,
			myFirstProgress: 0,
			mySecondProgress: 0,
			myThirdProgress: 0,

			userContact: {},
			realName: '',
			code: '',
			gender: '',
			birthday: ' ',
			mobile: '',
			telephone: '',
			email: '',
			qq: '',
			wechat: '',
			pageLoading: false
		};
	},
	onLoad() {
		this.pageOnLoad();
	},
	onShow() {
		
	},
	onPullDownRefresh: function() {
		this.pageOnLoad();
		console.log('刷新了');
		uni.stopPullDownRefresh();
	},
	components: {},
	methods: {
		pageOnLoad() {
			var me = this;
			me.pageLoading = true;
			// setTimeout(() => {
			// 	me.pageLoading = false;
			// }, 1500);
			var userInfo = me.getCurrentUserInfo();
			var serverUrl = me.getServerUrl();
			if (userInfo != null) {
				//debugger;
				me.getUserContact();
				me.userIsLogin = true;
				me.userInfo = userInfo;

				// var currentProjectInfo = me.getCurrentProjectInfo();
				// if (currentProjectInfo != null) {
				// 	me.currentProjectInfo = currentProjectInfo;
				// 	me.getProgress(currentProjectInfo.Id, 1, userInfo.UserName);
				// 	if (currentProjectInfo.IsSecondEnabled == 1) {
				// 		me.getProgress(currentProjectInfo.Id, 2, userInfo.UserName);
				// 	}
				// 	if (currentProjectInfo.IsThirdEnabled == 1) {
				// 		me.getProgress(currentProjectInfo.Id, 3, userInfo.UserName);
				// 	}
				// }
			} else {
				me.userIsLogin = false;
				me.userInfo = {};
				uni.reLaunch({
					url: 'login'
				});
			}
			me.pageLoading = false;
		},

		getProgress(projectId, piShift, userName) {
			let me = this;
			let serverUrl = me.getServerUrl();
			let userInfo = me.getCurrentUserInfo();

			uni.request({
				url: serverUrl + '/PhysicalInventoryOrder/GetLineStatistics/' + projectId + '?sequence=' + piShift + '&groupBy=OperatorUserName',
				method: 'POST',
				header: {
					appKey: me.appKey,
					appSecret: me.appSecret,
					userId: userInfo.Id,
					openId: userInfo.OpenId
				},
				success: res => {
					if (res.data.Status) {
						if (res.data.StatusCode == 'UserNotLogin') {
							uni.removeStorageSync('currentUserInfo');
							me.userIsLogin = false;
							uni.showToast({
								title: '用户已异地登录',
								duration: 2000,
								icon: 'none'
							});
						} else {
							if (res.data.Data == null) {
								//无数据
							} else {
								//console.log(res.data.Data);
								for (var i = 0; i < res.data.Data.length; i++) {
									if (res.data.Data[i].GroupBy == userName) {
										if (res.data.Data[i].TotalLines > 0) {
											var result = Math.ceil((res.data.Data[i].CountedLines / res.data.Data[i].TotalLines) * 100);
											if (piShift == 1) {
												me.myFirstCounted = res.data.Data[i].CountedLines;
												me.myFirstTotal = res.data.Data[i].TotalLines;
												me.myFirstProgress = result;
											}
											if (piShift == 2) {
												me.mySecondCounted = res.data.Data[i].CountedLines;
												me.mySecondTotal = res.data.Data[i].TotalLines;
												me.mySecondProgress = result;
											}
											if (piShift == 3) {
												me.myThirdCounted = res.data.Data[i].CountedLines;
												me.myThirdTotal = res.data.Data[i].TotalLines;
												me.myThirdProgress = result;
											}
										}
									}
								}
							}
						}
					} else {
						//debugger;
						uni.showToast({
							title: res.data.StatusMessage,
							duration: 2000,
							icon: 'none'
						});
						//return;
					}
				},
				fail: function() {
					uni.showToast({
						title: '网络异常请重试',
						duration: 2000,
						icon: 'none'
					});
				},
				complete: function() {
					setTimeout(function() {
						uni.hideLoading();
					}, 2000);
				}
			});
		},
		
		getUserContact() {
			let me = this;
			let serverUrl = me.getServerUrl();
			let userInfo = me.getCurrentUserInfo();
			uni.request({
				url: serverUrl + '/User/GetUserContact',
				method: 'POST',
				header: {
					appKey: me.appKey,
					appSecret: me.appSecret,
					userId: userInfo.Id,
					openId: userInfo.OpenId
				},
				success: res => {
					//console.log(res.data);
					//res.data = JSON.parse(res.data)
					if (res.data.Status) {
						if (res.data.StatusCode == 'UserNotLogin') {
							uni.removeStorageSync('currentUserInfo');
							me.userIsLogin = false;
							uni.showToast({
								title: '用户已异地登录',
								duration: 2000,
								icon: 'none'
							});
						} else {
							me.userContact = res.data.Data;
							me.realName = userInfo.RealName != null ? userInfo.RealName : '';
							me.code = userInfo.Code != null ? userInfo.Code : '';
							//me.gender = res.data.Data.Gender
							//me.birthday = res.data.Data.Birthday;
							me.mobile = res.data.Data.Mobile != null ? res.data.Data.Mobile : '';
							me.telephone = res.data.Data.Telephone != null ? res.data.Data.Telephone : '';
							me.email = res.data.Data.Email != null ? res.data.Data.Email : '';
							me.qq = res.data.Data.Qq != null ? res.data.Data.Qq : '';
							me.wechat = res.data.Data.WeChat != null ? res.data.Data.WeChat : '';
						}
					} else {
						//debugger;
						uni.showToast({
							title: res.data.StatusMessage,
							duration: 2000,
							icon: 'none'
						});
					}
				},
				fail: function() {
					uni.showToast({
						title: '网络异常请重试',
						duration: 2000,
						icon: 'none'
					});
				},
				complete: function() {
					setTimeout(function() {
						uni.hideLoading();
					}, 2000);
				}
			});
		},
		bindLogin() {
			uni.navigateTo({
				url: 'login'
			});
		},
		bindLogout() {
			var me = this;
			var userInfo = me.getCurrentUserInfo();
			var serverUrl = me.getServerUrl();

			uni.showLoading({
				title: '请求中...'
			});
			// console.log(userInfo);
			uni.request({
				url: serverUrl + '/User/Logout',
				method: 'POST',
				header: {
					appKey: me.appKey,
					appSecret: me.appSecret,
					userId: userInfo.Id,
					openId: userInfo.OpenId
				},
				success: res => {
					var statusmessage = res.data.StatusMessage;
					if (res.data.Status) {
						//debugger;
						//uni.clearStorageSync();
						uni.removeStorageSync('currentProjectInfo');
						uni.removeStorageSync('piConfig');
						uni.removeStorageSync('currentUserInfo');
						uni.showToast({
							title: '退出成功',
							//mask: true,
							duration: 2000,
							icon: 'success'
						});
						uni.reLaunch({
							url: 'login'
						});
					} else {
						//debugger;
						uni.clearStorageSync();
						uni.showToast({
							title: statusmessage,
							//mask: true,
							duration: 2000,
							icon: 'none'
						});
						uni.reLaunch({
							url: '../login/login'
						});
					}
				},
				fail: function() {
					uni.showToast({
						title: '网络异常请重试',
						duration: 2000,
						icon: 'none'
					});
				},
				complete: function() {
					setTimeout(function() {
						uni.hideLoading();
					}, 2000);
				}
			});
		}
	},
	onShareAppMessage(res) {
		return {
			title: '盘点App - 一款省时，省力，省钱的仓库盘点工具',
			path: '/pages/help/help'
		};
	}
};
</script>
<style>
.ucenter-face {
	width: 120rpx !important;
	height: 120rpx !important;
}
.ucenter-face image {
	width: 120rpx !important;
	height: 120rpx !important;
}
.ucenter-line {
	width: 100%;
	height: 10px;
	background: #f4f5f6;
	margin: 8px 0;
}
.btn-row {
	width: 90%;
	align-content: center;
}
</style>
